import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {StorageManagerProvider} from "../../providers/storage-manager/storage-manager";
import {AuthManagerProvider} from "../../providers/auth-manager/auth-manager";

/**
 * Generated class for the UserSettingsAccountInformationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings-account-information',
  templateUrl: 'user-settings-account-information.html',
})
export class UserSettingsAccountInformationPage {

  private name;
  private accountType;
  private clinicName;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storageManager: StorageManagerProvider, private auth: AuthManagerProvider) {

    if (this.auth.isMedical == false) {
      this.accountType = 'Patient';
    }
    else if (this.auth.isMedical == true) {
      if (this.auth.userGroup == 1) {
        this.accountType = 'Arzt';
      }
      else if (this.auth.userGroup == 2) {
        this.accountType = 'Aufnahme';
      }
    }
    this.name = this.auth.email;
    this.clinicName = this.auth.clinicName;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserSettingsAccountInformationPage');
  }

}



